.Card-InfoDescription {
    max-height: 60px;
    overflow: hidden;
    font-size: 14px;
    line-height: 20px;
}

.Card-InfoActions {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-direction: column;
        flex-direction: column;
    margin-left: 15px;
}

.Card-InfoActionsMore {
    width: 14px;
    height: 20px;
    background: url('More.png') no-repeat center;
    cursor: pointer;
}

.Card-InfoActionsLike {
    width: 17px;
    height: 16px;
    background: url('Like.png') no-repeat center;
    cursor: pointer;
}

.Card-InfoActionsChannel {
    display: none;
    width: 65%;
    font-weight: bold;
    font-size: 14px;
    line-height: 16px;
}



